import type { Color } from '../types';

export const getMoralisDefault = (color: Color) => {
    switch (color) {
        case 'blue':
            return moralisLogoDefaultBlue();
        case 'black':
            return moralisLogoDefaultBlack();
        default:
        case 'white':
            return moralisLogoDefaultWhite();
    }
};

